.project-section-container {
    border: 2px solid #ccc;
    border-radius: 5px;
    position: relative;
    padding: 20px;
}

.project-section-container:not(:last-child) {
    margin-bottom: 40px;
}

.project-section-container .section-header {
    display:block;
    border: 2px solid #ccc;
    background-color: #fff;
    color: #a6d194;
    border-radius: 5px;
    position: absolute;
    padding: 5px;
    top: -15px;
}



